.chat {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  /* background-color: lightblue; */
}

.chat__header {
  padding: 20px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid lightgray;
}

.chat__headerInfo {
  /* Flex: 1 inside one of the flex items makes it take all the space possible! */
  flex: 1;
  padding-left: 20px;
}

.chat__headerInfo > h3 {
  margin-bottom: 3px;
  font-weight: 500;
}

.chat__headerInfo > p {
  color: gray;
}

.chat__headerRight {
  display: flex;
  justify-content: space-between;
  min-width: 100px;
}

.chat__body {
  background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
  flex: 1;
  background-repeat: repeat;
  background-position: center;
  padding: 30px;
  overflow-y: scroll;
}

.chat__message {
  position: relative;
  font-size: 16px;
  padding: 10px;
  background-color: #ffffff;
  width: fit-content;
  border-radius: 10px;
  margin-bottom: 25px;
}

.chat__message:not(.chat__receiver) {
  padding-top: 20px;
}
.chat__name {
  position: absolute;
  top: 5px;
  font-weight: 800;
  font-size: xx-small;
}

.chat__timestamp {
  margin-left: 10px;
  font-size: xx-small;
}

.chat__receiver {
  background-color: #dcf8c6;
  margin-left: auto;
  /* Set the margin-left to auto so it takes the maximum possible */
}

.chat__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 62px;
  border-top: 1px solid lightgray;
}

.chat__footer > form {
  flex: 1;
  display: flex;
  /* background-color: lightblue; */
}

.chat__footer > form > input {
  flex: 1;
  border-radius: 30px;
  padding: 10px;
  border: none;
}

.chat__footer > form > button {
  /* We use this to maintain the functionality of the button in the form */
  display: none;
}

.chat__footer > .MuiSvgIcon-root {
  padding: 10px;
  color: gray;
}
