.sidebarChat {
  display: flex;
  padding: 15px;
  cursor: pointer;
  border-bottom: 1px solid #f6f6f6;
  background-color: white;
}

a {
  text-decoration: none !important;
  color: black;
}
.sidebarChat:hover {
  background-color: #f6f6f6;
}

.sidebarChat__info > h2 {
  font-size: 16px;
  margin-bottom: 8px;
}
.sidebarChat__info {
  margin-left: 15px;
}
